import { ContextApi } from 'contexts/Localization/types'
import { PageMeta } from './types'

export const DEFAULT_META: PageMeta = {
  title: 'Dead Token Exchange',
  description:
    'The most popular AMM on BSC by user count! Earn CAKE through yield farming or win it in the Lottery, then stake it in Syrup Pools to earn more tokens! Initial Farm Offerings (new token launch model pioneered by Dead Token Exchange), NFTs, and more, on a platform you can trust.',
  image: '',
}

export const getCustomMeta = (path: string, t: ContextApi['t']): PageMeta => {
  let basePath
  if (path.startsWith('/swap')) {
    basePath = '/swap'
  } else if (path.startsWith('/add')) {
    basePath = '/add'
  } else if (path.startsWith('/remove')) {
    basePath = '/remove'
  } else if (path.startsWith('/teams')) {
    basePath = '/teams'
  } else if (path.startsWith('/voting/proposal') && path !== '/voting/proposal/create') {
    basePath = '/voting/proposal'
  } else {
    basePath = path
  }

  switch (basePath) {
    case '/':
      return {
        title: `${t('Home')} | ${t('Dead Token Exchange')}`,
      }
    case '/swap':
      return {
        title: `${t('Exchange')} | ${t('Dead Token Exchange')}`,
      }
    case '/add':
      return {
        title: `${t('Add Liquidity')} | ${t('Dead Token Exchange')}`,
      }
    case '/remove':
      return {
        title: `${t('Remove Liquidity')} | ${t('Dead Token Exchange')}`,
      }
    case '/liquidity':
      return {
        title: `${t('Liquidity')} | ${t('Dead Token Exchange')}`,
      }
    case '/find':
      return {
        title: `${t('Import Pool')} | ${t('Dead Token Exchange')}`,
      }
    case '/pools':
      return {
        title: `${t('Pools')} | ${t('Dead Token Exchange')}`,
      }
    case '/profile':
      return {
        title: `${t('Your Profile')} | ${t('Dead Token Exchange')}`,
      }
    case '/profile/tasks':
      return {
        title: `${t('Pools')} | ${t('Dead Token Exchange Info & Analytics')}`,
        description: 'View statistics for Dead Token Exchange exchanges.',
      }
    default:
      return null
  }
}
